<template>
  <v-container>
    <v-breadcrumbs :items="breadcrumbs" class="pt-0">
      <template #divider>
        <v-icon icon="mdi-chevron-right"></v-icon>
      </template>
    </v-breadcrumbs>

    <div v-if="product" class="mb-4">
      <v-row>
        <v-col cols="12" md="4">
          <v-img
            :src="product.image"
            alt="Vendor Logo"
            class="rounded"
            height="200px"
            cover
          />
        </v-col>

        <v-col cols="12" md="8">
          <v-card-title>
            {{ product.title }}
          </v-card-title>
          <v-card-text>
            <p>{{ product.description }}</p>
          </v-card-text>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import { computed } from "vue";

import { useProductsStore } from "@/stores/productsStore";

export default {
  name: 'ProductView',
  props: {
    id: {
      type: String,
      required: true
    }
  },
  setup(props) {
    const productsStore = useProductsStore();

    const breadcrumbs = [
      {
        title: "Home",
        disabled: false,
        href: "/",
      },
      {
        title: "Product Details",
        disabled: true,
      },
    ];

    const product = computed(() => productsStore.getById(props.id));

    return {
      breadcrumbs,
      product,
    };
  }
}
</script>